*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root{
  --mainColor:#352030;
  --whiteColor:#fff;
  --blackColor:#000;
  --lightDart:#222;
}
body{
  font-family: 'Lato', sans-serif;
}
nav{
  width: 100%;
  height: 70px;
  line-height: 70px;
  padding: 0 100px;
  box-shadow: 0 1px 3px rgba(0,0,0,.1);
  display: flex;
  position: fixed;
  top: 0;
  background-color: #fff;
  z-index: 9999;
}
.left{
  text-align: left;
  width: 50%;
}
.left a{
  font-weight: 700;
  color: var(--mainColor);
  font-size: 21px;

}
.right{
  text-align: right;
  width: 50%;
}
nav ul{
  list-style: none;
}
nav ul a{
  text-decoration: none;
}
.shoppingCart i{
  display: inline-block;
  font-size: 20px;
  color: var(--lightDart);
  margin-right: 15px;

}
 .cartCount{
  display: inline-block;
  width: 35px;
  height: 35px;
  background: var(--mainColor);
  color: var(--whiteColor);
  border-radius: 30%;
  line-height: 35px;
  text-align: center;
  font-size: 12px;
  font-weight: bolder;
} 

/* Helpers */
/* .container{
  width: 100%;
  padding: 0 100px;
} */


/* Banner start*/

header{
  width: 100%;
  background: url("./assets/background.jpg");
  height: 450px;
  margin-top: 70px;
  background-size: cover;
  background-repeat: no-repeat;
  /* display: flex;
  align-items: center;
  justify-content: center; */
  color: #fff;
}
.headerText{
  background: rgba(0,0,0,.5);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.headerText h1{
  font-size: 35px;
  background: rgba(0,0,0,.2);
  height: auto;

}
.headerText p{
  margin-top: 7px;
}


.bannger_content h1{
  white-space: pre-line;
}


/* products style */
.products{

  transition: .4s;
}
.product:hover{
  transform: scale(1.1);
  
}
.robin{
  min-height: 100px;
  
  max-height: 100px;
  overflow: hidden;
  text-transform: uppercase;
  
}

