.product-details{
  padding: 15px 10px;
  text-align: center;
  background-color: var(--whiteColor);
  border-left: 1px solid black;
  border-right: 1px solid black;

}
.product-name{
  font-size: 18px;
  font-weight: 500;
  margin: 12px 0;
}
.product-price{
  font-size: 20px;
  font-weight: 300;
  margin: 12px 0;
}
.add-to-cart{
  background: var(--mainColor);
  padding: 10px 20px;
  width: 100%;
  color: var(--whiteColor);
  text-transform: uppercase;
  cursor: pointer;
  text-align: center;
  font-size: 14px;
  font-weight: bold;
}

/* Not Found */
.not-found{
  margin-top: 100px;
}
/* cart start */
.cart-container{
  width: 100%;
  margin-top: 70px;

  padding: 0 100px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}
.cart-details{
  width: 100%;
  border: 1px solid #fafafa;
  border-radius: 5px;
  
}
.cart{
  display: flex;
  align-items: center;
  background: var(--whiteColor);
  padding: 15px 20px;
}
.cart-image{
  display: inline-block;
  width: 80px;
  height: 80px;
  border-radius: 5px;
  overflow: hidden;
}
.cart-image img{
  height: 100%;
  width: 100%;
}
.cart-product-name{
  display: inline-block;
  width: 160px;
  padding: 0 15px;
  font-weight: bold;
  font-size: 14px;
  overflow-y: scroll;
}
.cart-product-price{
  display: inline-block;
  width: 120px;
  padding: 0 15px;
  text-align: center;
  font-weight: bold;
  color: var(--mainColor);
}

.inc{
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border: 1px solid var(--mainColor);
  font-size: 14px;
  color: var(--mainColor);
  border-radius: 4px;
  cursor: pointer;
}
.product-quantity{
  display: inline-block;
  padding: 0 15px;
  font-size: 15px;
}
.dec{
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border: 1px solid red;
  font-size: 14px;
  color: var(--mainColor);
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
}
.product-total-price{
  display: inline-block;
  width: 150px;
  padding: 0 15px;
  text-align: center;
}
.delete-product{
  border: none;
  color: rgb(121, 31, 31);
  cursor: pointer;
}



/* cart summary */
.cart-summary{
  width: 25%;
  padding: 0 0 0 20px;
  margin-top: 100px;
}
.summary{
  background: var(--whiteColor);
  padding: 13px;
}
.summary h3{
  font-size: 18px;
  font-weight: normal;
  border-bottom: 1px solid #999;
}
.total-items{
  display: flex;
  margin: 15px 0;

}
.items{
  width: 50%;
  text-align: left;
  font-weight: normal;
}
.items-count{
  width: 50%;
  text-align: right;
  font-weight: bold;
  color: var(--mainColor);
}
.total-price-section{
  display: flex;
  margin: 15px 0;
}
.just-title{
  width: 50%;
  text-align: left;
  font-weight: normal;
}
.item-price{
  width: 50%;
  text-align: right;
  font-weight: bold;
  color: var(--mainColor);

}


@media only screen and (max-width: 488px) {
  body {
    background-color: lightblue;
  }

  .cart{
    display: flex;
    flex-direction: column;
    
  
    text-align: center;
  }
  .cart-product-name{
    width: 100%;
    text-align: center;
  }
  .left li a{
    font-size: 20px;
  }
  nav{
    padding: 0;
    

  }

}

@media only screen and (max-width: 270px) {

  body{
    background-color: red;
  }
  .shoppingCart i{
    display: none;
  }
  nav{
    line-height: 30px;
    
    text-align: center;
  }
  .left{
    width: 50%;
  }
  .right{
    width: 50%;
  }



}

@media only screen and (max-width: 582px) {
  body{
    background: black;
  }


  .robin{
   
    min-height: 200px;
    max-width: 200px;

  }
  .headerText h1 {
    font-size: 21px;
 
}
}

